import { getGxCert } from "../gxcert-client";
import torusClient from "../torus";
import history from "../history";
import QRCode from "qrcode";
import config from "../config";
import * as modal from "./modal";
import * as input from "./input";
import * as request from "./request";
import * as session from "./session";
import * as fetch from "./fetch";
import wait from "./wait";

const sign = () => async (dispatch, getState) => {
  dispatch({
    type: "LOADING",
    payload: true,
  });
  let gxCert;
  try {
    gxCert = await getGxCert();
  } catch (err) {
    console.error(err);
    dispatch({
      type: "LOADING",
      payload: false,
    });
    return;
  }
  if (
    !window.confirm(
      ""
    )
  ) {
    dispatch({
      type: "LOADING",
      payload: false,
    });
    return;
  }
  const state = getState().state;
  const certificates = state.certificatesInIssuer;
  if (state.groupInSidebar === null) {
    modal.openModal("Please set group on sidebar.")(dispatch, getState);
    dispatch({
      type: "LOADING",
      payload: false,
    });
    return;
  }
  const image = state.image;
  if (!image) {
    modal.openModal("Image not set.")(dispatch, getState);
    dispatch({
      type: "LOADING",
      payload: false,
    });
    return;
  }
  let imageCid;
  try {
    imageCid = await gxCert.client.uploadImageToIpfs(image);
  } catch (err) {
    console.error(err);
    modal.openModal("IPFS")(
      dispatch,
      getState
    );
    dispatch({
      type: "LOADING",
      payload: false,
    });
    return;
  }
  const certificate = {
    context: {},
    title: state.title,
    description: state.description,
    image: imageCid,
    groupId: state.groupInSidebar.groupId,
  };
  if (!gxCert.client.isCertificate(certificate)) {
    modal.openModal("Invalid Certificate.")(dispatch, getState);
    dispatch({
      type: "LOADING",
      payload: false,
    });
    return;
  }
  let signed = null;
  try {
    signed = await gxCert.client.signCertificate(certificate, {
      address: gxCert.address(),
    });
  } catch (err) {
    console.error(err);
    modal.openModal("")(dispatch, getState);
    dispatch({
      type: "LOADING",
      payload: false,
    });
    return;
  }
  let transactionHash;
  try {
    transactionHash = await gxCert.client.createCert(signed);
  } catch (err) {
    console.error(err);
    if (err.message === "insufficient funds") {
      modal.openModal(
        "MATIC"
      )(dispatch, getState);
    } else {
      modal.openModal("")(
        dispatch,
        getState
      );
    }
    dispatch({
      type: "LOADING",
      payload: false,
    });
    return;
  }
  modal.openModal("", {
    link: "https://polygonscan.com/tx/" + transactionHash,
    text: "TransactionHash: " + transactionHash,
  })(dispatch, getState);
  let prevLength = certificates.length;
  await (() => {
    return new Promise((resolve, reject) => {
      const timer = setInterval(async () => {
        let certificates;
        try {
          certificates = await gxCert.getGroupCerts(
            certificate.groupId,
            dispatch,
            [
              {
                type: "certificate",
                refresh: true,
              },
              {
                type: "certificateImage",
                refresh: false,
                wait: false,
              },
            ]
          );
        } catch (err) {
          console.error(err);
          return;
        }
        if (prevLength < certificates.length) {
          clearInterval(timer);
          resolve();
        }
      }, 21000);
    });
  })();
  await fetch.fetchCertificatesInIssuer()(dispatch, getState);
  dispatch({
    type: "LOADING",
    payload: false,
  });

  history.push("/issue");
};

const addTo = () => async (dispatch, getState) => {
  let gxCert;
  try {
    gxCert = await getGxCert();
  } catch (err) {
    console.error(err);
    modal.openModal("")(dispatch, getState);
    return;
  }
  const state = getState().state;
  const email = state.toInIssue;
  let to;
  try {
    to = await torusClient.getPublicAddressByGoogle(email);
  } catch (err) {
    console.error(err);
    modal.openModal("")(dispatch, getState);
    return;
  }
  const usersToIssue = state.usersToIssue;
  for (const user of usersToIssue) {
    if (user.address === to) {
      return;
    }
  }
  let profile;
  try {
    profile = await gxCert.getProfile(to, dispatch, [
      {
        type: "profile",
        refresh: true,
      },
      {
        type: "profileImage",
        refresh: false,
        wait: true,
      },
    ]);
  } catch (err) {
    console.error(err);
    modal.openModal("")(dispatch, getState);
    return;
  }
  profile.address = to;
  usersToIssue.unshift(profile);
  dispatch({
    type: "ADD_TO",
    payload: usersToIssue,
  });
};

const removeUserInIssue = (address) => async (dispatch, getState) => {
  const state = getState().state;
  const users = state.usersToIssue;
  for (let i = 0; i < users.length; i++) {
    if (users[i].address === address) {
      users.splice(i, 1);
      dispatch({
        type: "ADD_TO",
        payload: users,
      });
      return;
    }
  }
};

const thisObjectToExport = {
  addTo,
  sign,
  removeUserInIssue,
};

const actions = {
  ...fetch,
  ...input,
  ...modal,
  ...request,
  ...session,
  ...thisObjectToExport,
};

export default actions;
