import React from "react";
import { Link } from "react-router-dom";

class Sidebar extends React.Component {
  componentDidMount() {
    this.props.fetchGroupsInSidebar();
  }
  render() {
    return (
      <div className="sidebar">
        <p className="sidebar-title"></p>
        <select
          className="sidebar-group"
          onChange={this.props.onChangeGroupInSidebar}
        >
          <option hidden></option>
          {this.props.groupsInSidebar
            ? this.props.groupsInSidebar.map((group) => {
                if (
                  this.props.groupInSidebar &&
                  group.groupId === this.props.groupInSidebar.groupId
                ) {
                  return (
                    <option value={group.groupId.toString()} selected>
                      {group.name}
                    </option>
                  );
                }
                return (
                  <option value={group.groupId.toString()}>{group.name}</option>
                );
              })
            : ""}
          <option value="new"></option>
        </select>
        {this.props.groupInSidebar ? (
          <ul>
            <li>
              <Link to="/issue"></Link>
            </li>
            <li>
              <Link to="/group"></Link>
            </li>
            <li>
              <Link to="/group/edit/"></Link>
            </li>
          </ul>
        ) : (
          ""
        )}
      </div>
    );
  }
}

export default Sidebar;
